ground-color: white;\r\n          .circle-digit {\r\n            opacity: 1;\r\n          }\r\n        }\r\n        &.active {          \r\n          transform: scale(1);\r\n          background-color: white;\r\n          .circle-label {\r\n            opacity: 1;\r\n            transition: opacity 1s 0.5s;\r\n          }\r\n          .circle-digit {\r\n            opacity: 1;\r\n          }\r\n        }\r\n      }\r\n\r\n      .circle-inside {\r\n        position: relative;\r\n        width: 100%;\r\n        height: 100%;\r\n      }\r\n\r\n      .circle-digit {\r\n        position: absolute;\r\n        top: 50%;\r\n        left: 50%;\r\n        transform: translate(-50%, -50%); \r\n        opacity: 0;  \r\n      }\r\n\r\n      .circle-label {\r\n        position: absolute;\r\n        opacity: 0;\r\n        left: 130%;\r\n        top: 50%;\r\n        transform: translateY(-50%); \r\n        transition: none;  \r\n      }\r\n    }\r\n  }\r\n\r\n  .bottom-flex {\r\n    display: flex;\r\n    flex-direction: column;\r\n    @include media-mobile {\r\n      flex-direction: column-reverse;\r\n    }     \r\n  }\r\n\r\n  .timeline-events {\r\n    position: relative;\r\n    opacity: 0;\r\n    max-width: 95%;\r\n    margin-left: 0;\r\n\r\n    .swiper-button-prev {\r\n      position: absolute;\r\n      left: -70px; \r\n      transform: scaleX(-1) translateY(-50%);\r\n      @include media-mobile {\r\n        display: none;\r\n      }\r\n    }\r\n    .swiper-button-next {\r\n      position: absolute;\r\n      right: -70px;\r\n      @include media-mobile {\r\n        display: none;\r\n      }\r\n    }\r\n    .swiper-button-disabled {\r\n      display: none;\r\n    }\r\n\r\n    &.active {\r\n      opacity: 1;\r\n      transition: opacity 0.3s ease-in-out;\r\n    }\r\n    .event-block {\r\n      padding: 10px;\r\n\r\n      .event-year {\r\n        font-family: 'Bebas Neue', sans-serif;\r\n        font-size: 25px;\r\n        color: $color-event-year;\r\n        margin-bottom: 0;\r\n 